@import "styles/variables";
@import "styles/mixins";

.section {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 23.5rem;
    left: calc(50% - 5rem);
    width: 100vw;
    height: 48.5rem;
    background-color: $colorMediumDark;
    border-top-left-radius: 1.5rem;
    border-bottom-left-radius: 30rem;
    opacity: 0.4;
  }
}

.circles {
  width: 68.2rem;
}

.wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 65rem 1fr;
  padding-bottom: 19rem;
}

.wrapperShadow {
  &::before {
    content: "";
    position: absolute;
    bottom: 14rem;
    right: -1.5rem;
    width: 65.44rem;
    height: 9.76rem;
    background-image: url("../../../assets/images/Shadow.png");
    background-size: cover;
    z-index: 2;
  }
}

.frame {
  position: absolute;
  top: 8.5rem;
  right: -10rem;
  clip-path: polygon(
      10.2rem 12.95rem,
      50.8rem -0.1rem,
      50.9rem 0,
      51rem 0.2rem,
      51.5rem 0.4rem,
      51.8rem 0.5rem,
      62rem 28rem,
      62.2rem 28.5rem,
      62.22rem 29.4rem,
      63.2rem 29.5rem,
      82rem 39.2rem,
      82.4rem 39.7rem,
      82.4rem 40.2rem,
      82.3rem 40.3rem,
      82.2rem 40.4rem,
      60rem 49.9rem,
      39rem 59rem,
      38.5rem 59.1rem,
      37.4rem 59rem,
      23.5rem 47rem,
      22.7rem 46.3rem,
      22.5rem 45.4rem,
      22.5rem 45.4rem,
      22.2rem 45.2rem,
      22rem 45rem,
      21.9rem 44.9rem,
      21.8rem 44.8rem,
      21.7rem 44.7rem,
      21.6rem 44.6rem,
      9.8rem 15rem,
      9.7rem 13.9rem,
      9.8rem 13.8rem,
      10rem 13.2rem,
      10.3rem 12.8rem,
  );
}

.video {
  width: 82.6rem;
}

.labelNext {
  @include nav($colorDarkest, 500, true);

  position: absolute;
  top: calc(64.2rem + 7rem);
  left: 7rem;
  display: flex;
  flex-direction: column;
  letter-spacing: 4px;
  transform: rotate(-90deg);
  transform-origin: 0 50%;

  span {
    padding-left: 12.6rem;
    text-transform: lowercase;
  }
}

.arrow {
  width: 64.2rem;
  fill: $colorHighlight;
}

.textBlock {
  position: relative;
  padding-top: 32rem;
}

.description {
  @include descriptionPromo;

  margin: 3.5rem 0 0 0;
  padding-right: 40%;
}

.button {
  margin-top: 5.5rem;
}

@media (max-width: $adaptiveMax) {
  .section {
    &::before {
      top: 37rem;
      left: 2rem;
      width: 100vw;
      height: 10.6rem;
      background-color: $colorMediumDark;
      border-top-left-radius: 1.5rem;
      border-bottom-left-radius: 30rem;
      opacity: 0.4;
    }
  }

  .wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-bottom: 3rem;
  }

  .labelNext,
  .button {
    display: none;
  }

  .wrapperShadow {
    &::before {
      top: 38.4rem;
      bottom: auto;
      right: calc(50% - 14rem);
      width: 31rem;
      height: 10.6rem;
      background-size: contain;
      background-repeat: no-repeat;
    }
  }

  .frame {
    order: 2;
    position: relative;
    top: auto;
    left: calc(50% - 17rem);
    margin-top: 2rem;
    margin-left: calc(-1 * var(--padding-global-h));
    clip-path: polygon(
      4.5rem 5.1rem,
      20.5rem 0rem,
      20.8rem 0.1rem,
      20.9rem 0.1rem,
      21rem 0.2rem,
      21.1rem 0.3rem,
      25.4rem 11.9rem,
      25.3rem 11.95rem,
      25.9rem 12.1rem,
      33.45rem 16rem,
      33.45rem 16.2rem,
      33.5rem 16rem,
      33.45rem 16.39rem,
      16rem 23.9rem,
      15.5rem 24rem,
      15.2rem 24rem,
      15.0rem 23.9rem,
      9.25rem 18.8rem,
      9.2rem 18.6rem,
      9.2rem 18.4rem,
      8.9rem 18.25rem,
      8.85rem 18.2rem,
      4rem 6rem,
      4rem 5.6rem,
      4.1rem 5.5rem,
      4.15rem 5.3rem,
      4.5rem 5.1rem
    );
  }

  .video {
    width: calc(30.7rem + 2.05 * var(--padding-global-h));
  }

  .textBlock {
    order: 1;
    padding-top: 1.2rem;
  }

  .heading {
    text-align: center;
  }

  .description {
    margin: 0;
    padding-right: 0;
    text-align: center;
  }

  .buttonAdaptive {
    order: 3;
    margin: 0 auto;
    width: 16rem;
  }

  .buttonScroll {
    @include resetButton;

    order: 4;
    align-self: center;
    margin-top: 0.7rem;
    width: 4rem;
    height: 4rem;
    background-color: $colorHighlight;
    border-radius: 50%;
  }

  .iconArrow {
    width: 1.4rem;
    stroke: $colorLightest;
  }
}
