@import "styles/variables";
@import "styles/mixins";

.list {
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 2.5rem;
  padding: 0.8rem 1.5rem;
  background-color: $colorMediumDarkTransparent;
  border-radius: 3rem;
  border: 2px solid $colorMediumDark;

  &::before {
    content: "";
    position: absolute;
    top: 2px;
    left: 2px;
    width: 50%;
    height: calc(100% - 4px);
    background-color: $colorLightest;
    border-radius: 3rem;
    transition-duration: $transitionDuration;
  }

  & > * {
    position: relative;
  }
}

.listSelectedBefore {
  &::before {
    left: calc(50% - 2px);
  }
}

.button {
  @include resetButton;
  @include nav($colorHighlight);

  display: flex;
  text-align: left;
}

.buttonBefore {
  color: $colorMediumDarker;
}

.icon {
  margin-right: 0.8rem;
  width: 4rem;
}
