@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
  width: 79rem;
  background-color: $colorLightest;
  border-radius: 5rem;
  z-index: 1;
}

.slide {
  padding: 6rem 3rem 2rem 31rem;
  height: 47rem;
}

.description {
  margin-top: 2rem;
  color: $colorDarkest;
}

.label {
  @include regular($colorDarkest, 700);

  display: block;
  margin-top: 2rem;
}

.button {
  @include resetButton;

  --size: 5.8rem;
  position: absolute;
  bottom: calc(-1 * var(--size) / 2);
  display: flex;
  justify-content: center;
  align-items: center;
  width: var(--size);
  height: var(--size);
  background-color: $colorHighlight;
  border-radius: 50%;
  border: 2px solid $colorLightest;
  transition-duration: $transitionDuration;
  z-index: 1;

  &:hover {
    .icon {
      stroke: $colorHighlightDark;
    }
  }

  &:active {
    background-color: $colorLightest;
    border-color: $colorHighlight;

    .icon {
      stroke: $colorHighlight;
    }
  }

  &:not(.buttonNext) {
    left: 32rem;

    .icon {
      transform: rotate(180deg);
    }

    .labelButton {
      right: calc(100% + 1rem);
    }
  }
}

.buttonNext {
  right: 32rem;

  .labelButton {
    left: calc(100% + 1rem);
  }
}

.labelButton {
  @include regular;

  position: absolute;
  bottom: 0;
  line-height: 1;
  transition-duration: $transitionDuration;
}

.icon {
  width: 1.5rem;
  stroke: white;
  transition-duration: $transitionDuration;
}

.buttonDisabled {
  background-color: $colorLightest;
  border-color: $colorHighlight;
  pointer-events: none;

  .icon {
    stroke: $colorHighlight;
  }

  .labelButton {
    opacity: 0.6;
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    width: 100%;
    border-radius: 3rem;
  }

  .slide {
    padding: 14rem 1.8rem 1.8rem 1.8rem;
    height: auto;
  }

  .button {
    --size: 4rem;
    bottom: -1.9rem;

    &:not(.buttonNext) {
      left: 9rem;
    }

    &.buttonNext {
      right: 9rem;
    }
  }

  .labelButton {
    bottom: -0.5rem;
    font-size: 1.2rem;
    line-height: 1.4rem;
  }
}
