@import "styles/variables";
@import "styles/mixins";

.wrapper {
  --paddingHeader: 3.6rem;
  position: absolute;
  top: 0;
  left: calc(50% - 144rem / 2);
  display: flex;
  align-items: flex-start;
  margin: 0 auto;
  padding-bottom: var(--paddingHeader);
  padding-top: var(--paddingHeader);
  width: 144rem;
  z-index: 1;
}

.icon {
  width: 8.3rem;
  fill: $colorHighlight;
}

.button {
  @include nav;

  display: flex;
  align-items: center;
  margin-left: auto;
  transition-duration: $transitionDuration;

  &:hover {
    color: $colorMediumDark;

    .phone {
      color: $colorMediumDark;
    }
  }
}

.phone {
  @include nav($colorHighlight, 700);

  margin-left: 4rem;
  transition-duration: $transitionDuration;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    --paddingHeader: 1.2rem;
    position: static;
    left: 0;
    align-items: center;
    width: 100%;
    border-bottom: 3px solid $colorMediumDark;

    .icon {
      width: 7.1rem;
    }

    .button {
      @include nav($colorHighlight, 400);

      margin-left: auto;
      text-decoration: underline;
    }

    .iconPhone {
      margin-left: 2rem;
      width: 2.4rem;
      fill: $colorHighlight;
    }
  }
}
