@import "styles/variables";

.sliderVideo {
  margin: 7.1rem 0 0 8.2rem;
  width: 43.7rem;
  height: 28.5rem;
}

.video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: $adaptiveMax) {
  .sliderVideo {
    margin-top: 0.8rem;
    margin-left: calc(50% - 22.1rem / 2 + 0.45rem);
    width: 21.5rem;
    height: 14.3rem;

    :global {
      .swiper-slide {
        //width: 100% !important;
      }
    }
  }
}
