@import "../../../styles/variables";
@import "../../../styles/mixins";

.wrapper {
  position: relative;
  padding-top: 12rem;
  padding-bottom: 11rem;
  overflow: hidden;
  border-radius: var(--section-border-radius);

  &::before {
    content: "";
    position: absolute;
    top: 26.7rem;
    left: 13rem;
    width: 59.5rem;
    height: 43.2rem;
    background-image: url("/assets/images/slider_template.png");
    background-size: cover;
    z-index: 10;
  }

  &::after {
    content: "";
    position: absolute;
    top: 28rem;
    left: 21rem;
    width: 45rem;
    height: 40rem;
    background-color: $colorLightest;
  }
}

.heading,
.description {
  position: relative;
  grid-column: 1 / -1;
  z-index: 1;
}

.description {
  @include nav($colorLightest, 400);

  margin: 0.5rem 0 0 0;
}

.sliders {
  display: grid;
  grid-template-columns: 22rem 7rem 79rem;
  margin-top: 2.2rem;

  .video {
    position: relative;
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    z-index: 3;
  }

  .info {
    grid-column: 3 / 5;
    grid-row: 1 / 2;
  }
}

.button {
  position: relative;
  grid-column: 1 / -1;
  margin-top: 4rem;
  margin-left: auto;
  min-width: 15.8rem;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-top: 5rem;
    padding-bottom: 5rem;

    &::before {
      top: 11rem;
      left: calc(50% - 29.3rem / 2);
      width: 29.3rem;
      height: 21rem;
    }


    &::after {
      display: none;
    }
  }

  .heading,
  .description {
    text-align: center;
  }

  .sliders {
    grid-template-columns: 100%;
    grid-template-rows: 10rem 11.5rem auto;

    .video {
      position: relative;
      grid-column: 1 / 2;
      grid-row: 1 / 3;
      z-index: 3;
    }

    .info {
      grid-column: 1 / 2;
      grid-row: 2 / 4;
    }
  }

  .button {
    margin: 4rem auto 0 auto;
  }
}
