// screen sizes
$adaptiveMax: 1200px;

// colors
$colorLightest: rgb(255, 255, 255);
$colorMedium: rgb(242, 242, 242);
$colorHighlight: rgb(254, 77, 61);
$colorHighlightDark: rgb(180, 15, 0);
$colorMediumDark: rgb(196, 196, 196);
$colorMediumDarker: rgb(169, 169, 169);
$colorMediumDarkTransparent: rgba(196, 196, 196, 0.4);
$colorDarkest: rgb(45, 45, 45);
$colorCounter: rgb(0, 0, 0);

// transitions
$transitionDuration: 0.3s;

// spaces
body {
  :global {
    --section-border-radius: 7rem;
    --padding-global-h: 13rem;

    @media (max-width: $adaptiveMax) {
      --padding-global-h: 1.5rem;
      --section-border-radius: 3rem;
    }
  }
}
