@import "styles/variables";
@import "styles/mixins";
@import "styles/functions";

.button {
  @include resetButton;

  transition-duration: $transitionDuration;
}

.buttonStandard {
  height: 5rem;
}

.buttonSmall {
  height: 4rem;
}

.buttonShaped {
  @include fontButtonShaped($colorHighlight);

  padding-right: 3rem;
  padding-left: 3rem;
  border-radius: 3rem;
}

.buttonDark {
  color: $colorLightest;
  background-color: $colorHighlight;
  border: 2px solid $colorHighlight;
  box-shadow:
    0 10rem 8rem getButtonShadowColor(0.07),
    0 4.2rem 3.3rem getButtonShadowColor(0.05),
    0 2.2rem 1.8rem getButtonShadowColor(0.04),
    0 1.3rem 1rem getButtonShadowColor(0.035),
    0 0.66rem 0.5rem getButtonShadowColor(0.028),
    0 0.28rem 0.22rem getButtonShadowColor(0.019);

  &:hover {
    background-color: $colorMediumDark;
    border-color: $colorMediumDark;
  }

  &:active {
    color: $colorHighlight;
    background-color: $colorLightest;
    border-color: $colorHighlight;
  }
}

.buttonLight {
  color: $colorHighlight;
  background-color: $colorLightest;
  border: 2px solid $colorLightest;
  box-shadow:
    0 10rem 8rem getButtonShadowColor(0.07),
    0 4.2rem 3.3rem getButtonShadowColor(0.05),
    0 2.2rem 1.8rem getButtonShadowColor(0.04),
    0 1.3rem 1rem getButtonShadowColor(0.035),
    0 0.66rem 0.5rem getButtonShadowColor(0.028),
    0 0.28rem 0.22rem getButtonShadowColor(0.019);

  &:hover {
    color: $colorMediumDark;
    border-color: $colorMediumDark;
  }

  &:active {
    color: $colorLightest;
    background-color: $colorHighlight;
    border-color: $colorLightest;
  }
}
