@import "styles/mixins";

.wrapper {
}

.label {
  @include step;

  position: relative;
  display: block;
  margin-left: 1rem;
  padding: 0 0.5rem;
  letter-spacing: 0.2rem;
  text-align: center;
  text-transform: lowercase;
  writing-mode: sideways-lr;
  background-color: white;

  &::before {
    content: "";
    position: absolute;
    top: calc(50% - 4.5rem / 2);
    left: -0.9rem;
    width: 4.5rem;
    height: 4.5rem;
    border: 2px solid $colorHighlight;
    border-radius: 50%;
    z-index: -1;
  }
}

.icon {
  margin-top: 1rem;
  margin-left: 1.3rem;
}
