@import "styles/mixins";

.listMore {
  padding-top: 0.5rem;
}

.item {
  &:not(:first-child) {
    margin-top: 1rem;
  }
}

.buttonCollapse {
  @include resetButton;
  @include regular($colorDarkest, 900);

  display: block;
  margin: 3rem auto 0 auto;
  text-align: center;
}

.buttonScroll {
  margin-top: 3rem;
  font-size: 1.4rem;
  line-height: 1.6rem;
}

.buttonCollapseOpening {
  opacity: 0;
}

.buttonCollapseOpened {
  display: none;
}
